o know</h3>\r\n            </div>\r\n            <div>\r\n                <form @submit.prevent=\"subscribe($event)\">\r\n                    <label for=\"subscribe\">\r\n                        <input\r\n                            type=\"email\"\r\n                            id=\"subscribe\"\r\n                            name=\"subscribe\"\r\n                            v-model=\"subscriptions\"\r\n                            required\r\n                            :placeholder=\"placeholder\"\r\n                        />\r\n                    </label>\r\n                    <div\r\n                        style=\"\r\n                            background: #461952;\r\n                            border-radius: 7.11111px 7.11111px 0px 0px;\r\n                            width: 44.44px;\r\n                            height: 44.44px;\r\n                            display: flex;\r\n                            justify-content: center;\r\n                        \"\r\n                    >\r\n                        <button\r\n                            type=\"submit\"\r\n                            style=\"\r\n                                outline: none;\r\n                                border: none;\r\n                                background-color: inherit;\r\n                            \"\r\n                        >\r\n                            <i\r\n                                class=\"fas fa-chevron-right\"\r\n                                style=\"color: #ffffff; align-self: center\"\r\n                            ></i>\r\n                        </button>\r\n                    </div>\r\n                </form>\r\n                <div>Subscribe to Happywedding</div>\r\n            </div>\r\n        </div> -->\r\n        <flashMessage :message=\"message\"></flashMessage>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport flashMessage from \"../FlashMessage.vue\";\r\n\r\nexport default {\r\n    components: {\r\n        flashMessage,\r\n    },\r\n    data() {\r\n        return {\r\n            faqList: [\r\n                {\r\n                    id: 1,\r\n                    question: \"Can i change my plan?\",\r\n                    answer: \"Yes you can\",\r\n                },\r\n                {\r\n                    id: 2,\r\n                    question: \"Is there a free trial available?\",\r\n                    answer: \" No You have to choose a plan to log in to happy wed\",\r\n                },\r\n                {\r\n                    id: 3,\r\n                    question: \"What is the cancellation policy?\",\r\n                    answer: \"Yes you can\",\r\n                },\r\n                {\r\n                    id: 4,\r\n                    question: \"Can other info be added to an invoice?\",\r\n                    answer: \"Yes you can\",\r\n                },\r\n                {\r\n                    id: 5,\r\n                    question: \"How does billing works?\",\r\n                    answer: \"Yes you can\",\r\n                },\r\n                {\r\n                    id: 6,\r\n                    question: \"How to i change my account email?\",\r\n                    answer: \"Yes you can\",\r\n                },\r\n            ],\r\n            subscriptions: \"\",\r\n            placeholder: \"Enter Email Address\",\r\n            message: null,\r\n        };\r\n    },\r\n    methods: {\r\n        toggle(e) {\r\n            if ($(e.currentTarget).next().css(\"display\") == \"block\") {\r\n                $(e.currentTarget).next().css(\"display\", \"none\");\r\n                $(e.currentTarget).find(\".btn-toggle\").text(\"+\");\r\n                return;\r\n            }\r\n            $(\".faq-single\").each(function (index, item) {\r\n                $(item).find(\".btn-toggle\").text(\"+\");\r\n                $(item).children().eq(1).css(\"display\", \"none\");\r\n                console.log();\r\n            });\r\n\r\n            $(e.currentTarget).next().css(\"display\", \"block\");\r\n            $(e.currentTarget).find(\".btn-toggle\").text(\"-\");\r\n        },\r\n        subscribe(e) {\r\n            let _this = this;\r\n            let formData = new FormData();\r\n            let data = _this.subscriptions;\r\n            formData.append(\"subscriptions\", data);\r\n            axios\r\n                .post(\"/news\", formData)\r\n                .then(function (response) {\r\n                    _this.subscriptions = null;\r\n                    _this.message =\r\n                        \"Subscription request submitted successfully\";\r\n                    setTimeout(function () {\r\n                        _this.message = null;\r\n                    }, 3000);\r\n                })\r\n                .catch((errors) => {\r\n                    _this.subscriptions = null;\r\n                    _this.message = \"Subscription request failed, try again\";\r\n                    setTimeout(function () {\r\n                        _this.message = null;\r\n                    }, 3000);\r\n                    console.log(errors);\r\n                });\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.first-sec {\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n.first-sec h4 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 17.9162px;\r\n    line-height: 27px;\r\n    color: #000000;\r\n}\r\n.first-sec h1 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    font-size: 42.2428px;\r\n    line-height: 63px;\r\n    color: #000000;\r\n}\r\n.first-sec h3 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 13.9348px;\r\n    line-height: 21px;\r\n    color: #000000;\r\n}\r\n.second-sec {\r\n    background-color: #f8f8fa;\r\n    display: flex;\r\n    justify-content: space-around;\r\n    padding: 3em 0;\r\n}\r\n.second-sec >