lor: #000000;\r\n}\r\n.third-sec h3 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 13.9348px;\r\n    line-height: 21px;\r\n    color: #000000;\r\n}\r\n.third-sec form {\r\n    display: flex;\r\n}\r\n.third-sec form input {\r\n    border: none;\r\n    border-bottom: 2px solid #000000;\r\n    background-color: #ffffff !important;\r\n    height: 44px;\r\n}\r\n.third-sec form input::placeholder {\r\n    font-family: \"DM Sans\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 13.3333px;\r\n    line-height: 17px;\r\n    /* identical to box height */\r\n\r\n    letter-spacing: -0.181818px;\r\n\r\n    color: #000000;\r\n\r\n    mix-blend-mode: normal;\r\n    opacity: 0.5;\r\n}\r\n.third-sec > div:nth-child(2) > div {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 9.29915px;\r\n    line-height: 14px;\r\n    letter-spacing: -0.126807px;\r\n\r\n    color: #959595;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
